<template lang="pug">
  .im-dialog(:class="{active, push}")
    .im-dialog__info
      span.im-dailog__pic
        img(v-if="this.getInfo.id != info.persons[0].id" :src= "info.persons[0].photo")
        img(v-else :src= "info.persons[1].photo")
      span.im-dialog__name {{info.groupDialog ? info.name : dialogPerson.fullName}}
      span.im-dialog__push(v-if="push > 0") {{push}}
</template>

<script>
import moment from 'moment'
import {mapGetters} from "vuex";
export default {
  name: 'ImDialog',
  props: {
    active: Boolean,
    push: Number,
    online: Boolean,
    me: Boolean,
    info: Object
  },
  computed: {
    ...mapGetters('profile/info', ['getInfo']),
    dialogPerson() {
      if (this.info.groupDialog || !this.getInfo) {
        return null;
      }

      if (this.getInfo.id != this.info.persons[0].id) {
        return this.info.persons[0];
      } else {
        return this.info.persons[1];
      }
    },
  }
}
</script>

<style lang="stylus">
@import '../../assets/stylus/base/vars.styl';

.im-dialog {
  display: flex;
  align-items: center;
  height: 100px;
  position: relative;
  padding: 0 6.38%;
  z-index: 0;
  cursor: pointer;

  &:before {
    content: '';
    display: none;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #F8FAFD;
    z-index: -1;
  }

  &:after {
    content: '';
    display: block;
    height: 1px;
    width: 87.24%;
    background-color: #E3E8EE;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  &.push + &.active, &.active + &.push {
    &:after {
      display: none;
    }
  }

  &.active {
    &:after {
      display: none;
    }

    &:before {
      display: block;
    }
  }

  &.push {
    &:after {
      display: none;
    }

    &:before {
      display: block;
      background-color: #E9F5EF;
    }

    .im-dialog__content {
      max-width: 30%;
    }

    .im-dialog__time {
      color: #7D9A8B;
    }
  }
}

.im-dailog__pic {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;
  flex: none;
}

.im-dialog__info {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 150px;

  @media (max-width: breakpoint-xl) {
  }
}

.im-dialog__name {
  font-size: 15px;
  line-height: 22px;
  color: steel-gray;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.im-dialog__content {
  max-width: 37%;

  @media (max-width: breakpoint-xl) {
    display: none;
  }
}

.im-dialog__last {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 13px;
  line-height: 18px;
  letter-spacing: -0.003em;

  &-me {
    color: eucalypt;
  }
}

.im-dialog__time {
  color: #A4A4B8;
  font-size: 13px;
  line-height: 19px;
}

.im-dialog__push {
  width: 27px;
  height: 27px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 27px;
  font-weight: 600;
  color: #fff;
  background-color: #E65151;
  margin-left: auto;
}
</style>



